import $http from "@/common/api/request.js"
import IndexSwiper from "@/components/index/IndexSwiper.vue"
import Recommend from "@/components/index/Recommend.vue"
import Card from "@/components/common/Card.vue"
import CommodityList from "@/components/common/CommodityList.vue"
import Banner from "@/components/index/Banner.vue"
import Icons from "@/components/index/Icons.vue"
import Hot from "@/components/index/Hot.vue"
import Shop from "@/components/index/Shop.vue"
export default {
	data() {
		return {
			// 选中的下标值
			topBarIndex:0,
			//顶栏跟随的索引id值
			scrollIntoIndex:"top0",
			//内容块的高度值
			clentHeight:0,
			//顶栏数据
			topBar:[],
			//承载数据
			newTopBar:[]
		}
	},
	components:{
		IndexSwiper,
		Recommend,
		Card,
		CommodityList,
		Banner,
		Icons,
		Hot,
		Shop
	},
	onLoad() {
		this.__init();
	},
	onReady(){
		// let view = uni.createSelectorQuery().select(".home-data");
		// view.boundingClientRect(data => {
		//   this.clentHeight = 10000
		//   // data.height
		// }).exec();
		
		uni.getSystemInfo({
			success: (res) => {
				this.clentHeight = res.windowHeight - uni.upx2px(80) - this.getClientHeight();
			}
		})
	},
	//标题栏按钮点击
	onNavigationBarButtonTap(e){
		// console.log(e)
		if(e.float === 'left'){
			uni.navigateTo({
				url:'/pages/search/search'
			})
		}
	},
	methods: {
		//请求首页
		__init(){
			
			$http.request({
				url:"/index_list/data"
			}).then((res)=>{
				this.topBar = res.topBar;
				this.newTopBar = this.initData(res)
			}).catch(()=>{
				uni.showToast({
					title:'请求失败',
					icon:'none'
				})
			})
			
			// uni.request({
			// 	url:"http://192.168.0.106:3000/api/index_list/data",
			// 	success: (res) => {
			// 		// console.log(res.data.data)
			// 		let data = res.data.data;
			// 		this.topBar = data.topBar;
			// 		this.newTopBar = this.initData(data)
			// 	}
			// })
		},
		//添加数据
		initData(res){
			let arr = [];
			for (let i = 0; i < this.topBar.length; i++) {
				let obj = {
					data:[],
					load:"first",
					loadText:"上拉加载更多..."
				}
				//录入第一个数据
				if(i==0){
					obj.data = res.data
				}
				arr.push(obj)
			}
			return arr;
		},
		//点击顶栏
		changeTab(index){
			if(this.topBarIndex === index){
				return ;
			}else{
				this.topBarIndex = index;
				this.scrollIntoIndex = "top" + index;
				
				// console.log(this.newTopBar[this.topBarIndex].load)
				
				// 每一次滑动赋值为first
				if(this.newTopBar[this.topBarIndex].load==='first'){
					this.addData();
				}
				
			}
		},
		//对应滑动
		onChangeTap(e){
			this.changeTab(e.detail.current);
		},
		//兼容可视区高度【兼容】
		getClientHeight(){
			const res = uni.getSystemInfoSync();
			// console.log(res.platform,res.statusBarHeight);
			const system = res.platform;
			if(system === 'ios'){
				return 44 + res.statusBarHeight;
			}else if(system === 'android'){
				return res.statusBarHeight - 25;
			}else{
				return 0;
			}
		},
		//对应显示不同数据
		addData(callback){
			//拿到索引
			let index = this.topBarIndex
			//拿到id===>不同的板块 推荐：1； 运动户外：2； 服饰内衣：3；
			let id = this.topBar[index].id
			//请求不同的数据
			let page = Math.ceil(this.newTopBar[index].data.length / 5) + 1
			
			//请求数据
			$http.request({
				url:"/index_list/"+id+"/data/"+page+""
			}).then((res)=>{
					this.newTopBar[index].data = [...this.newTopBar[index].data,...res]
				}).catch(()=>{
				uni.showToast({
					title:'请求失败',
					icon:'none'
				})
			})
			
			// uni.request({				
			// 	url:"http://192.168.0.106:3000/api/index_list/"+id+"/data/"+page+"",
			// 	success: (res) => {
			// 		// console.log(res.statusCode)
			// 		if(res.statusCode!=200){
			// 			return;
			// 		}else{
			// 			let data = res.data.data
			// 			// console.log(data)
			// 			this.newTopBar[index].data = [...this.newTopBar[index].data,...data]
			// 		}
			// 	}
			// })
			
			// console.log(this.newTopBar[index].data)
			
			//当请求结束后，重新赋值
			this.newTopBar[this.topBarIndex].load = 'last'
			if(typeof callback==='function'){
				callback()
			}
		},
		//上拉加载更多
		loadMore(index){
			this.newTopBar[this.topBarIndex].loadText = '加载中...';
			
			//请求完数据，文字提示信息换成【上拉加载更多...】
			this.addData(()=>{
				this.newTopBar[this.topBarIndex].loadText = '上拉加载更多...'
			})
		}
	}
		
}
